<script>
  import HeaderTop from "../../components/Headertop/HeaderTop";
  import ShopList from "../../components/ShopList/ShopList"
  import Swiper from "swiper"
  import "swiper/dist/css/swiper.min.css"
  import Axios from "axios"
  let address = "广东省深圳市龙岗区宝南路83号"
  let first = true
  export default {
    data(){
      return{
        address
      }
    },
    mounted(){
      if(first){
        this.getAddress()
        first = false
      }
      //创建swiper对象
      var mySwiper = new Swiper ('.swiper-container', {
        //loop: true, // 循环模式选项
        // 如果需要分页器
        pagination: {
          el: '.swiper-pagination',
        }
      })
    },
    components: {ShopList, HeaderTop},
    methods:{
      getAddress(){
        //alert(555)
        if(navigator.geolocation){
          navigator.geolocation.getCurrentPosition(this.showPosition,this.showError,{
            // 指示浏览器获取高精度的位置，默认为false
            enableHighAcuracy: true,
            // 指定获取地理位置的超时时间，默认不限时，单位为毫秒
            timeout: 3000,
            // 最长有效期，在重复获取地理位置时，此参数指定多久再次获取位置。
            maximumAge: 1000
          });
        }else{
          alert('获取地址失败!');
          this.getAddressByIp(returnCitySN["cip"])
        }
      },
      showPosition(position){
        let lat=position.coords.latitude;
        let lng=position.coords.longitude;
        this.getAddressByLL(`${lat},${lng}`)
      },
      showError(error){
        switch(error.code)
        {
          case error.PERMISSION_DENIED:
            alert("用户拒绝对获取地理位置的请求。")
            break;
          case error.POSITION_UNAVAILABLE:
            alert("位置信息是不可用的。")
            break;
          case error.TIMEOUT:
            alert("请求用户地理位置超时。")
            break;
          case error.UNKNOWN_ERROR:
            alert("未知错误。")
            break;
        }
        this.getAddressByIp(returnCitySN["cip"])
      },
      getAddressByIp(ip) {
        Axios.get("waimai_api/getAddressByIp/"+ip)
          .then(res=>{
            if(res.data.code===0){
              address = this.address = res.data.address
            }else{
              console.log("获取地址信息失败")
            }
          })
          .catch(err=>{
            console.log(err);
          })
      },
      getAddressByLL(ll) {
        Axios.get("waimai_api/getAddressByLL/"+ll)
          .then(res=>{
            if(res.data.status===0){
              address = this.address = res.data.result.formatted_address
              //alert(address)
            }else{
              console.log("获取地址信息失败")
            }
          })
          .catch(err=>{
            console.log(err);
          })
      }
    }
  }

</script>

<style scoped>
  .kinds{
    width: 100%;
