    overflow: hidden;
  }
  .kinds ul li{
    width: 20%;
    height: 50%;
    float: left;
    box-sizing: border-box;
    padding: 10px;
    overflow: hidden;
  }
  .kinds ul a{
    display: inline-block;
    text-align: center;
  }
  .kinds li span{
    display: inline-block;
    width: 100%;
    text-align: center;
    max-width: 60px;
  }
  .kinds li img{
    width: 70%;
    height: 70%;
    object-fit: cover;
    margin: 0 auto;
    border-radius: 50%;
  }
  .kinds li p{
    text-align: center;
    font-size: 0.7rem;
    color: #999;
    height: 30%;
  }

</style>



