<template>
  <div class="header">
    <slot name="left"></slot>
    <slot name="right"></slot>
    <p>
      <span class="ellipsis">{{title}}</span>
    </p>
  </div>
</template>

<script>
    export default {
      name: "HeaderTop",
      props:{
        title:String
      }
    }
</script>

<style scoped>
  .header{
    background-color: #02a774;
    width: 100%;
    height: 50px;
    overflow: hidden;
  }
  .header>p{
    margin: 0 auto;
    width: 50%;
    text-align: center;
  }
  .header p>span{
    display: inline-block;
    width: 100%;
  }
  .header span{
    height: 100%;
    line-height: 50px;
    color: #fffeee;
    text-align: center;
    font-size: 1.3rem;
  }
  .header .left{
    float: left;
    width: 25%;
    box-sizing: border-box;
    padding-top: 5px;
  }

  .header .right{
    float: right;
    width: 25%;
    font-size: 0.75rem;
  }
  .header .right a{
    margin-right: 3px;
  }
</style>




