import Vue from 'vue'
import Router from 'vue-router'
import Home from '../pages/Home/Home'
import Order from '../pages/Order/Order'
import Search from '../pages/Search/Search'
import Mine from '../pages/Mine/Mine'
import Login from '../pages/Login/Login'

Vue.use(Router)
export default new Router({
  model:'history',
  routes: [
    {
      path: '/home',
      component:Home,
      meta:{
        FootGuide: true
      }
    },
    {
      path: '/order',
      component:Order,
      meta:{
        FootGuide: true
      }
    },{
      path: '/search',
      component:Search,
      meta:{
        FootGuide: true
      }
    },{
      path: '/mine',
      component:Mine,
      meta:{
        FootGuide: true
      }
    },
    {
      path: '/',
      redirect:'/home'
    },
    {
      component:Login,
      path:'/login',
    }
  ]
})



